/* CustomNavbar.css */
.custom-navbar {
    background-color: #E3D6BB !important; /* Custom background color */
    padding: 0.5rem 1rem; /* Adjust top and bottom padding, keep horizontal padding */
}

.custom-brand {
    font-weight: bold;
    color: #1A1A1D !important;
    font-size: 1.5rem; /* Larger brand font size */
    margin-right: auto; /* This will push everything else to the right */
}

/* Hides the toggle button by making it transparent */
.custom-navbar .navbar-toggler {
    background: none; /* Remove the default background */
    border: none; /* Removes border */
}

.custom-navbar .navbar-toggler-icon {
    background-image: none; /* Hide the default icon */
}

.custom-navbar .navbar-toggler:focus {
    box-shadow: none; /* Removes focus outline */
    outline: none; /* Additional removal of focus outline for some browsers */
}

.nav-link {
    font-size: 1.2rem; /* Increase font size for nav links */
    margin: 0 0.5rem; /* Space between nav items */
    color: #1A1A1D !important; /* Override default color */
}

.nav-link:hover {
    color: #FFFFFF !important;
}

/* Ensure icon and text are aligned properly */
.custom-link .nav-link {
    display: flex;
    align-items: center;
}

.custom-link .nav-link svg {
    margin-right: 0.5rem; /* Space between icon and text */
}

/* Navbar collapse content alignment */
.navbar-collapse {
    justify-content: flex-end; /* Align nav items to the right */
}
